@import url(https://raw.githack.com/Kerthin/links/main/css/pradoFontLinks.css);
* {
  margin:0;
  padding:0;  
  box-sizing:border-box;
  background-color: #1f1f1f;
}

::-webkit-scrollbar {
  width: 0;
}

.containerSlider {
  position: fixed;
  float: left;
  width: 100%;
  height: 100%;
  background-color: #1f1f1f;
  top: -100%;
  transition: all 1s;
  z-index: 10;
}

.containerSlider.open {
  top: 0;
}

.swiper-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
  transition-property: -webkit-transform;
  transition-property: transform;
  transition-property: transform, -webkit-transform;
  box-sizing: content-box;
}

.swiper-slide {
  position: relative;
  background-color: #1f1f1f;
  width: 1200px;
  height: 100vh;
  border-right: 1px solid #5d5b53;
  overflow-y: scroll;
  cursor: grab;
}
.swiper-slide:last-child {
  border-right: 0px;
}
.swiper-slide:active {
  cursor: grabbing;
}

.collectionTitle {
  position: fixed;
  bottom: 0;
  width: 518.5px;
  height: 60px;
  background-color: #1f1f1f;
  z-index: 888;
  padding: 20px 0;
}
.collectionTitle__name {
  text-align: center;
  font-family: 'Alliance';
  font-size: 16px;
  font-weight: 100;
  color: #cfc8b7;
}

.poster {
  width: 100%;
  height: 100%;
  padding: 50px 5px;
  overflow: hidden;
}
.poster__info {
  position: relative;
  width: 100%;
  height: 100%;
  margin-top: 50px;
}
.poster__textWrap {
  position: relative;
  width: 100%;
  height: calc(100% - 470px);
}
.poster__text {
  position: absolute;
  width: 100%;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.poster__imgWrap {
  max-width: 100%;
  height: auto;
}
.poster__img {
  max-width: 100%;
  height: auto;
  overflow: hidden;
  cursor: pointer;
  -webkit-transform: translateY(-5%);
          transform: translateY(-5%);
}

.poster__imgBg {
  background-size: cover;
  max-width: 100%;
  height: auto;
  object-fit: contain;
  transition: all .5s;
  -webkit-transform: scale(0.75);
          transform: scale(0.75);
}

.poster__img_img {
  object-fit: contain;
}

.poster__imgBg:hover {
  -webkit-transform: scale(0.75);
          transform: scale(0.75);
}
.poster__name,
.poster__years {
  text-align: center;
  font-family: sans-serif;
  color: #cfc8b7;
}
.poster__name {
  font-size: 28px;
  font-weight: 100;
}
.poster__years {
  margin-top: 15px;
  font-family: Arno;
  font-size: 20px;
  color: #666566;
}


