}
.wrap {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
    width: 30px;
    height: 16px;
    cursor: pointer;
    /*background-color: pink;*/
  }

  .item {
    position: absolute;
    width: 100%;
    height: 4px;
    background-color: #fff;
    border-radius: 10px;
    transition: all 0.5s;
  }

  .itemFirst {
    top: 0;
  }
  .itemSecond {
    bottom: 0;
  }

.wrap.close .itemFirst {
    -webkit-animation: burgerTop 0.6s ease-in-out forwards, burgerRotateTop 0.6s ease-in-out 0.4s forwards;
            animation: burgerTop 0.6s ease-in-out forwards, burgerRotateTop 0.6s ease-in-out 0.4s forwards;
  }
  .wrap.close .itemSecond {
    -webkit-animation: burgerBottom 0.6s ease-in-out forwards, burgerRotateBottom 0.6s ease-in-out 0.4s forwards;
            animation: burgerBottom 0.6s ease-in-out forwards, burgerRotateBottom 0.6s ease-in-out 0.4s forwards;
  }
  .wrap.open .itemFirst {
    top: 6px;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    -webkit-animation: burgerTop2 0.6s ease-in-out 0.4s forwards, burgerRotateTop2 0.6s ease-in-out forwards;
            animation: burgerTop2 0.6s ease-in-out 0.4s forwards, burgerRotateTop2 0.6s ease-in-out forwards;
  }
  .wrap.open .itemSecond {
    bottom: 6px;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    -webkit-animation: burgerBottom2 0.6s ease-in-out 0.4s forwards, burgerRotateBottom2 0.6s ease-in-out forwards;
            animation: burgerBottom2 0.6s ease-in-out 0.4s forwards, burgerRotateBottom2 0.6s ease-in-out forwards;
  }
  
  .itemFirst {
    top: 0;
  }
  .itemSecond {
    bottom: 0;
  }
  
  @-webkit-keyframes burgerTop {
    0% {
      top: 0;
    }
    50% {
      top: 6px;
    }
    100% {
      top: 6px;
    }
  }
  
  @keyframes burgerTop {
    0% {
      top: 0;
    }
    50% {
      top: 6px;
    }
    100% {
      top: 6px;
    }
  }
  @-webkit-keyframes burgerRotateTop {
    0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    50% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(45deg);
              transform: rotate(45deg);
    }
  }
  @keyframes burgerRotateTop {
    0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    50% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(45deg);
              transform: rotate(45deg);
    }
  }
  
  @-webkit-keyframes burgerBottom {
