.body {
    background-color: #1f1f1f;
}

.mainContainer {
    background-color: #1f1f1f;
    width: auto;
    height: 100%;
}

.topSidebar {
    position: fixed;
    top: 0;
    width: 100%;
    height: 60px;
    background-color: #1f1f1f;
    z-index: 1111;
    padding: 20px 0;
    border-bottom: 1px solid #5d5b53;
  }


.topSidebar__burgerWrap {
    display: block;
    float: left;
    width: 25%;
    height: 100%;
    /*background-color: brown;*/
  }

.topSidebar__nameWrap {
    display: block;
    float: left;
    width: 26%;
    height: 100%;
    /*background-color: green;*/
  }
.topSidebar__name {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    color: #cfc8b7;
    font-family: 'Arno';
    font-size: 25px;
    font-weight: 500;
  }
  
.topSidebar__eventsWrap {
    position: relative;
    display: block;
    float: left;
    width: calc(49% - 110px);
    height: 100%;
    /*background-color: purple;*/
  }
.topSidebar__events {
    position: absolute;
    top: 50%;
    left: 55%;
    transform: translateX(-55%) translateY(-50%);
    font-family: 'Alliance';
    font-size: 16px;
    font-weight: 500;
    /*background-color: pink;*/
  }
.topSidebar__event {
    color: #cfc8b7;
  }
.topSidebar__slash {
    margin: 0 15px;
  }
  
.topSidebar__ticketsWrap {
    position: relative;
    display: block;
    float: left;
    width: 110px;
    height: 100%;
    /*background-color: pink;*/
  }
.topSidebar__tickets {
    position: absolute;
    top: 50%;
    right: 0;
    color: #cfc8b7;
    font-family: 'Alliance';
    font-size: 16px;
    font-weight: 500;
  }

.menuTopSidebar, .topSidebar {
    height: 50px;
}

.mainPage {
    background-color: #1f1f1f;
    z-index: 1;
    min-height: 100%;
    height: 100%;
}

.mainImg {
    display: block;
  margin-left: auto;
  margin-right: auto;
  transform: scale(0.85);
  z-index: 1;
}