.wrap {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
    width: 30px;
    height: 16px;
    cursor: pointer;
    /*background-color: pink;*/
  }

  .item {
    position: absolute;
    width: 100%;
    height: 4px;
    background-color: #fff;
    border-radius: 10px;
    transition: all 0.5s;
  }

  .itemFirst {
    top: 0;
  }
  .itemSecond {
    bottom: 0;
  }

.wrap.close .itemFirst {
    animation: burgerTop 0.6s ease-in-out forwards, burgerRotateTop 0.6s ease-in-out 0.4s forwards;
  }
  .wrap.close .itemSecond {
    animation: burgerBottom 0.6s ease-in-out forwards, burgerRotateBottom 0.6s ease-in-out 0.4s forwards;
  }
  .wrap.open .itemFirst {
    top: 6px;
    transform: rotate(45deg);
    animation: burgerTop2 0.6s ease-in-out 0.4s forwards, burgerRotateTop2 0.6s ease-in-out forwards;
  }
  .wrap.open .itemSecond {
    bottom: 6px;
    transform: rotate(45deg);
    animation: burgerBottom2 0.6s ease-in-out 0.4s forwards, burgerRotateBottom2 0.6s ease-in-out forwards;
  }
  
  .itemFirst {
    top: 0;
  }
  .itemSecond {
    bottom: 0;
  }
  
  @keyframes burgerTop {
    0% {
      top: 0;
    }
    50% {
      top: 6px;
    }
    100% {
      top: 6px;
    }
  }
  @keyframes burgerRotateTop {
    0% {
      transform: rotate(0deg);
    }
    50% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(45deg);
    }
  }
  
  @keyframes burgerBottom {
    0% {
      bottom: 0;
    }
    50% {
      bottom: 6px;
    }
    100% {
      bottom: 6px;
    }
  }
  @keyframes burgerRotateBottom {
    0% {
      transform: rotate(0deg);
    }
    50% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(-45deg);
    }
  }
  
  
  
  
  @keyframes burgerTop2 {
    0% {
      top: 6px;
    }
    50% {
      top: 6px;
    }
    100% {
      top: 0;
    }
  }
  @keyframes burgerRotateTop2 {
    0% {
      transform: rotate(45deg);
    }
    50% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(0deg);
    }
  }
  
  @keyframes burgerBottom2 {
    0% {
      bottom: 6px;
    }
    50% {
      bottom: 6px;
    }
    100% {
      bottom: 0;
    }
  }
  @keyframes burgerRotateBottom2 {
    0% {
      transform: rotate(-45deg);
    }
    50% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(0deg);
    }
  }
  
